import Vue from 'vue'
import VueRouter from 'vue-router'
import  Home from '@/components/Home.vue'
import  TopicList from '../components/TopicList.vue'
import Topic from "../components/Topic";
import Login from "../components/Login";
import Center from "../components/Center";
import Msg from "../components/Msg";
import Logout from "../components/Logout";
import User from "../components/User";
import Create from "../components/Create";
import Collect from "../components/Collect";
import Edit from "../components/Edit";

Vue.use(VueRouter);//全局注册路由

//定义路由信息对象
const routes = [
  // {
  //   path:'/',
  //   name:'home',
  //   component:Home
  // }
  {
    path: '/',
    redirect:'/topiclist/all'
  },
  {
    path:'/topiclist/:name',
    name:'topiclist',
    component: TopicList,
    props:true
  },
  {
    path:'/topic/:id',
    name:'topic',
    component:Topic,
    props:true
  },
  //登录
  {
    path:'/login',
    name:'login',
    component:Login
  },
  //发表话题
  {
    path:'/create',
    name:'create',
    component:Create
  },
  //话题列表
  {
    path:'/collect',
    name:'collect',
    component:Collect
  },
  // 个人中心
  {
    path:'/center',
    name:'center',
    component:Center
  },
  {
    path:'/user/:loginname',
    name:'user',
    component:User,
    props:true
  },
  // 消息通知
  {
    path:'/msg',
    name:'msg',
    component:Msg
  },
  // 退出
  {
    path:'/logout',
    name:'logout',
    component:Logout
  },
  //编辑主题
  {
    path:'/topic/:id/edit',
    name:'edit',
    component:Edit,
    props:true
  }
];
//创建路由管理器
const router = new VueRouter({
  routes
});
//对外输出模块
export default router;



