
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: "user",
props:['loginname'],
components:{
Card,
Group,
Cell
},
data:function(){
return{
userinfo:null,
collect_topics:[]
}
},
beforeRouteEnter:function (to,from,next) {
next(function (vm) {
vm.$http.get('/user/'+to.params.loginname).then(function (response) {

vm.userinfo = response.data.data;
});

vm.$http.get('/topic_collect/'+to.params.loginname).then(function (response) {
vm.collect_topics = response.data.data;
});
});
},
beforeRouteUpdate:function (to,from,next) {
let _this = this;
this.$http.get('/user/'+to.params.loginname).then(function (response) {

_this.userinfo = response.data.data;
});

_this.$http.get('/topic_collect/'+to.params.loginname).then(function (response) {
_this.collect_topics = response.data.data;
});
next();
}
}



