
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapMutations} from 'vuex'
export default {
name: "login",
components:{
Group,
XInput,
XButton,
Toast
},
data:function () {
return{
accesstoken:'',
isShowToast:false,
toastText:''
};
},
methods:{
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken','mutationAuthorId']),

dologin:function () {

let _this = this;
this.$http.post('/accesstoken',
{accesstoken:this.accesstoken}).then(function (response) {







if (response && response.data && response.data.success){

_this.mutationLogin();
_this.mutationLoginname(response.data.loginname);
_this.mutationAccessToken(_this.accesstoken);
_this.mutationAuthorId(response.data.id);

window.localStorage.setItem('loginStatus',true);
window.localStorage.setItem('loginname',response.data.loginname);
window.localStorage.setItem('accesstoken',_this.accesstoken);
window.localStorage.setItem('author_id',response.data.id);

_this.$router.push('/user/'+ response.data.loginname);
}
}).catch(function (error) {


_this.isShowToast = true;
_this.toastText = error.response.data.error_msg;
});
}
}
}



