
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState} from 'vuex';
export default {
name:'topiclist',
props:['name'],
components:{
Group,
Cell,
Scroller,
Tab,
TabItem
},
computed:{
...mapState(['topics'])
},
data:function(){
return{
lists:[],

page:1,

limit:15,

pullupConfig:{
content:'上拉加载更多',
downContent:'松开进行加载',
upContent:'上拉加载更多',
loadingContent:'加载中...'
},

pulldownConfig:{
content: '下拉刷新消息',
downContent:'松开进行刷新',
upContent: '下拉刷新消息',
loadingContent:'加载中...'
}
};
},
methods:{

getDescByTab(tab){
return this.$store.getters.getTopicDescByTab(tab)
},

routeto(index){







this.$router.push('/topiclist/'+this.topics[index].name);
},

freshData(){
let _this = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function (response) {
if (response.data && response.data.success){
_this.list = response.data.data;

_this.$refs.pullup.donePulldown();
}
})
},

loadMoreData(){

let _this = this;
this.page = this.page+1;
this.$http.get('/topics',{
params:{

tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function (response) {
if (response.data.success){


for (let i=0;i<response.data.data.length;i++){
_this.lists.push(response.data.data[i]);
}


_this.$refs.pullup.donePullup();
}
});
}
},


beforeRouteEnter:function (to,from,next) {




next(function (vm) {

vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function (response) {




if(response.data.success){
vm.lists = response.data.data;
}
});
})
},

beforeRouteUpdate:function (to,from,next) {


let _this = this;
this.$http.get('/topics',{
params:{

tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function (response) {
if (response.data.success){

_this.lists = response.data.data;
}
});

next();
}
}



